.pt-module{
	& + .pt-module,
	& + .panel{
		margin-top: @padding-base-vertical;
	}
}

.panel + .pt-module{
	margin-top: @padding-base-vertical;
}

.pt-order-info{
	padding: @padding-base-vertical @padding-base-horizontal;
	.order-info-item{
		&:last-child{
			.order-info-cell{
				border-right: none;
			}
		}
	}
	.order-info-cell{
		padding: @padding-base-vertical @padding-large-horizontal;
		border-right: 1px solid @divide-color;
	}
	.order-info-icon{
		float: left;
		margin-right: -48px;
		position: relative;
		z-index: 2;
		&,img{
			width: 48px;
			height: 48px;
		}
	}
	.order-info-container{
		float: left;
		width: 100%;
	}
	.order-info-main{
		padding-left: 48 + @padding-large-horizontal;
		padding-top: @padding-small-vertical;
	}
	.order-info-num,
	.order-info-cont{
		line-height: @line-height-normal;
		text-align: center;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.order-info-num{
		font-size: 24px;
		color: @text-color;
	}
	.order-info-cont{
		font-size: @font-size-base;
		color: @text-weaker-color;
	}
}

.pt-news-list{
	padding-left: @padding-base-horizontal;
	padding-right: @padding-base-horizontal;
	.news-item{
		padding: @padding-base-vertical 0;
	}
	.news-item-img{
		margin-right: -180px;
		position: relative;
		display: table;
		text-align: center;
		z-index: 2;
		float: left;
		width: 180px;
		height: 90px;
		overflow: hidden;
		background: @gray-brighter;
		border: 2px solid @gray-lighter;
		.item-img-cell{
			display: table-cell;
			vertical-align: middle;
			position: relative;
			z-index: 5;
			img{
				display: inline-block;
				max-width: 180px;
				max-height: 90px;
			}
		}
	}
	.news-item-container{
		float: left;
		width: 100%;
		p{
			margin:0;
			padding:0;
		}
	}
	.news-item-main{
		padding-left: 180 + @padding-large-horizontal;
	}
	.news-item-tit{
		font-size: @font-size-large;
		font-weight: bold;
		color: @text-color;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		& + .news-item-cont{
			padding-top: @padding-base-vertical;
		}
	}
	.news-item-cont{
		color: @text-weak-color;
		font-size: @font-size-base;
		height: 40px;
		overflow: hidden;
		& + .news-item-footer{
			padding-top: @padding-base-vertical;
		}
	}
	.news-item-footer{
		color: @text-weaker-color;
		font-size: @font-size-base;
	}
}


.pt-notice-box{
	padding: @padding-base-vertical @padding-large-horizontal;
	position: relative;
	.notice-icon{
		position: absolute;
		left: @padding-large-horizontal;
		top: 50%;
		margin-top: -9px;
		font-size: 18px;
		color: @brand-warning;
	}
	.notice-list{
		padding-left: @padding-large-horizontal*2;
	}
	.notice-list,
	.ontice-item{
		height: 20px;
		overflow: hidden;
	}
	.ontice-item{
		line-height: 20px;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		a{
			display: block;
			white-space: nowrap;
			text-overflow: ellipsis;
			overflow: hidden;
		}
	}
}

.pt-notice-list{
	padding-left: @padding-base-horizontal;
	padding-right: @padding-base-horizontal;
	.notice-item{
		padding: @padding-base-vertical @padding-base-horizontal;
		border-bottom: 1px solid @divide-color;
		&:last-child{
			border-bottom: none;
		}
	}
	.notice-item-tit,
	.notice-item-cont{
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.notice-item-tit{
		font-size: @font-size-large;
		color: @text-color;
		& + .notice-item-cont{
			padding-top: @padding-small-vertical;
		}
	}
	.notice-item-cont,
	.notice-item-footer{
		font-size: @font-size-base;
	}
	.notice-item-cont{
		color: @text-weak-color;
		& + .notice-item-footer{
			padding-top: @padding-base-vertical;
		}
	}
	.notice-item-footer{
		color: @text-weaker-color;
	}
}

.pt-msg-modal-body{
