.pt-idx-grab-order{
	padding: @padding-base-vertical @padding-base-horizontal;
	.grab-order-cell{
		.panel{
			border-top: 4px solid @border-color;
			.panel-body{
				padding-bottom: @padding-large-horizontal;
			}
		}
		&.active{
			.panel{
				background: @gray-bright;
			}
		}
	}
	.grab-order-user-info{
		text-align: center;
		padding: @padding-base-vertical @padding-base-horizontal;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		.grab-user-info{
			border-radius: 12px;
			border: 1px solid @border-color;
			position: relative;
			display: inline-block;
			text-align: center;
			padding: @padding-xs-vertical @padding-base-horizontal;
			background: @block-content-bg;
		}
		.grab-user-img{
			position: absolute;
			left: -8px;
			top: 50%;
			margin-top: -16px;
			background: @gray-bright;
			border: 1px solid @border-color;
			z-index: 3;
			overflow: hidden;
			&,img{
				height: 32px;
				width: 32px;
				border-radius: 16px;
			}
			& + .grab-user-name{
				padding-left: @padding-base-horizontal*3;
			}
		}
		.grab-user-name{
			display: block;
		}
	}
	.grab-order-type,
	.grab-order-name,
	.grab-order-state{
		padding-top: @padding-base-vertical;
		text-align: center;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}
	.grab-order-type{
		color: @text-weaker-color;
		font-size: @font-size-base;
	}
	.grab-order-number{
		text-align: center;
		padding: @padding-base-vertical @padding-base-horizontal;
		.num-group{
			display: inline-block;
			margin: 0 @padding-xs-horizontal;
		}
		.num-item{
			display: inline-block;
			font-size: @font-size-base;
			color: @btn-info-color;
			background: @btn-info-bg;
			border:1px solid @btn-info-border;
			border-radius: @border-radius-base;
			padding: @padding-small-vertical @padding-xs-horizontal;
		}
	}
}

