//mixins
.pt-text-overflow(@v){
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}
//组件
.pt-make-row-none-padding(){
	.col(@index) {
	    @item: ~".pt-none-padding-row>.col-xs-@{index},.pt-none-padding-row>.col-sm-@{index}, .pt-none-padding-row>.col-md-@{index},.pt-none-padding-row>.col-lg-@{index}";
	    .col((@index + 1), @item);
	}
	.col(@index, @list) when (@index =< 12) {
		@item: ~".pt-none-padding-row>.col-xs-@{index},.pt-none-padding-row>.col-sm-@{index}, .pt-none-padding-row>.col-md-@{index}, .pt-none-padding-row>.col-lg-@{index}";
		.col((@index + 1), ~"@{list}, @{item}");
	}
	.col(@index, @list) when (@index > 12) { // terminal
		@{list} {
		   padding-left: 0;
		   padding-right: 0;
		}

	}
	.col(1);
}

.pt-none-padding-row{
	margin-right:0;
	margin-left:0;
}
.pt-make-row-none-padding;
.pt-equal-layout{
	position: relative;
	display: table;
	width: 100%;
	.equal-item{
		position: relative;
		border-right: none;
		display: table-cell;
		vertical-align: top;
		float: none;
		&:not(:first-child){
			&:before{
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				content:'';
				width: 1px;
				background: @divide-color;
			}
		}

	}
}
//箭头面包屑
.pt-breadcrumb{
	background: @block-content-bg;
	margin: @padding-base-vertical @padding-base-horizontal 0;
	padding: 0;
	list-style-type: none;
	>li{
		display: inline-block;
		vertical-align: middle;
		&+li{
			&:before{
				padding: 0 5px;
				content:'>';
				color: @text-weaker-color;
				display: inline-block;
			}
		}
	}
	.breadcrumb-tip{
		&+li:before{
			content:':';
			font-weight: 700;
		}
	}
}
