		vertical-align: middle;
		font-weight: 700;
	}
}
//pt-fixed-height
.pt-result-list{
	max-height: 150px;
	overflow-y: auto;
	overflow-x: hidden;
	.pt-result-item{
		.pt-text-overflow(ellipsis);
	}
}
//
.pt-subpanel-normal{
	.panel-heading{
		border-bottom: 1px solid @divide-color;
		padding: @padding-base-vertical @padding-base-horizontal;
		.panel-subtitle{
			height: 18px;
			line-height: 18px;
			font-weight: 700;
		}
	}
}
//具体页面
.pt-panel-subtitle{
	line-height: @input-height-base;
	display: inline-block;
	font-weight: 700;
}
.pt-upload-horizontal-wrap{
	float: left;
	margin-left: 92px;
}

.upload-tool{
	// margin-right: @padding-base-horizontal*2;
	width: 92px;
	float: left;
	margin-right:-92px;
}
.pt-upload-horizontal-list{
	margin-right: -@padding-base-horizontal*2;
	li{
		float: left;
		border-right:  1px solid @divide-color;
		margin-bottom: @padding-base-vertical;
		&:first-child{
			border-left: 1px solid @border-color;
		}
		.upload-item{
			position: relative;
			width: 130px;
			margin: 0 @padding-base-horizontal*2;
			padding-right: 20px;
			.pt-text-overflow(ellipsis);
			.close{
				position: absolute;
				right: 0;
				top: 0;
			}
		}
	}
}
.pt-approve-flow{
	position: relative;
	margin: 0 @padding-base-horizontal*4 0 @padding-base-horizontal*4 + 130;
	&:before{
		position: absolute;
		content: '';
		display: block;
		left: 0;
		top: 0;
		bottom: 0;
		width: 1px;
		background: @border-color;
	}
	.flow-item{
		position: relative;
		padding-top: @padding-large-vertical;
		&:last-child{
			padding-bottom: @padding-large-vertical;
		}
	}
	.flow-step{
		position: absolute;
		width: 130px;
		left: -130px;
		.pt-text-overflow(ellipsis);
		padding-right: @padding-large-horizontal+2;
		top: @padding-large-vertical;
		text-align: right;
	}
	.flow-container{
		margin-left: @padding-large-horizontal+2;
		.flow-info{
			padding: @padding-small-vertical @padding-base-horizontal;
			background: @block-accent-bg;
			margin-top: @padding-base-vertical;
		}
		.flow-header{
			.user-name,
			.user-addr,
			.flow-static{
				margin-right: @padding-base-horizontal;
			}
		}
	}
	.dot{
		position: absolute;
		display: block;
		width: 10px;
		height: 10px;
		left: -5px;
		top: @padding-large-vertical+2;
		// margin-left: -4px;
		// margin-top: -4px;
		border-radius: 1000px;
		&.dot-default{
			background: @border-color;
		}
		&.dot-warning{
			background: @brand-warning;
		}
	}
}

.pt-no-config{
	position: relative;
	margin: 0 auto;
	padding-top: 80px;
	.no-config-img{
		img{
			margin: 0 auto;
			display: block;
			width: 486px;
			height: auto;
		}
	}
	.no-config-msg{
		text-align: center;
		margin-bottom: 40px;
		p{
			margin:0;
			padding:0;
		}
		.msg-title{
			h1,h2{
				margin:0;
				font-weight: normal;
				color: @text-color;
			}
			h1{
				font-size: 40px;
				margin-bottom: 10px;
			}
			h2{
				font-size: 30px;
			}
			margin-bottom: 26px;
		}
		.msg-content{
			font-size: 14px;
			color: @text-weak-color;
		}
	}
}
//登录页找回密码
.pt-find-psw-wrap{
	padding: @padding-base-vertical*2 @padding-base-horizontal*3;
	.pt-find-psw-form{
		.form-group{
			margin-bottom: @padding-base-vertical*2;
		}
		.control-label{
			padding-top: @padding-base-vertical + 2;
			text-align: left;
		}
		.input-group-addon{
			&.input-group-vcode-addon{
				padding: 0;
				line-height: 1;
				img{
					height: @input-height-large - 2;
					vertical-align: top;
					width: 100px;
				}
			}
		}
	}
}

//屏幕锁定
.pt-unlock-wrap{
	position: relative;
	padding: @padding-base*2;
	.unlock-icon{
		float: left;
		margin-right: -100px;
		&,img{
			width: 100px;
			height: 82px;
		}
	}
	.unlock-container{
		float: left;
		width: 100%;
		.unlock-main{
			padding-left: 100px + @padding-base*3;
		}
	}
	.unlock-main{
		padding: 0;
	}
	.unlock-title{
		font-size: 18px;
		color: @text-color;
	}
	.unlock-cont{
		font-size: 12px;
		color: @text-weak-color;
	}
	.unlock-title,
	.unlock-cont{
		& + .unlock-form{
			margin-top: 10px;
		}
	}
}

//浏览器选择
.pt-browser-download{
	padding: @padding-base*3;
	.browser-main{
		position: relative;
		padding-left: 60px + @padding-large-horizontal;
		&::before{
			content: '\e804';
			font-family: "iconfont" !important;
			font-size: 60px;
			color: #775CBC;
			position: absolute;
			left: 0;
			top: -16px;
		}
	}
	.browser-title{
		font-size: 24px;
		color: @text-color;
	}
	.browser-cont{
		font-size: @font-size-base;
		color: @text-weaker-color;
	}
	.browser-row{
		margin-top: 30px;
		margin-left: -@padding-base-horizontal;
		margin-right: -@padding-base-horizontal;
		.clearfix;
		.browser-col{
			padding: 0 @padding-base-horizontal;
			float: left;
			width: 50%;
		}
		.browser-cell{
			padding: @padding-base*4;
			background: @block-content-bg;
			border: 1px solid @border-color;
			&:hover{
				box-shadow: 0 8px 16px 0 rgba(0,0,0,0.08);
			}
		}
		.browser-icon{
			margin: 0 auto @padding-large-horizontal auto;
			&,img{
				width: 72px;
				height: 72px;
			}
		}
		.browser-title{
			font-size: @font-size-base;
			text-align: center;
		}
		.browser-control{
			margin-top: 24px;
			text-align: center;
		}
	}
	.browser-footer{
		margin-top: @padding-large-horizontal;
		text-align: right;
	}
}


//礼包选择
.pt-package-slick{
	margin-bottom: 0;
	.slick-item{
		margin: 0;
		border: 1px solid @divide-color;
		background: #f8f8f8;
		height: 100px;
		margin-left: 4px;
		margin-right: 4px;
	}
	.slick-dots{
		position: static;
		bottom: 0;
	}
}
