html,
body {
    position: relative;
    height: 100%;
}

body {
    font-style: normal;
    background: @portal-body-bg;
    min-width: @portal-min-width;
}

.animated {
    animation-duration: .5s;
}

.form-horizontal .control-label {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.app-view {
    position: relative;
    min-height: 100%;
}

.ui-tabs.tabs-border {
    border-width: 0;
}

.ui-workspace-tabs {
    position: relative;
    .head-tabs-nav {
        position: relative;
        height: @portal-tabs-height;
        overflow: hidden;
    }
    .main-tabs-nav {
        height: @portal-tabs-height;
        background-color: @portal-tabs-bg;
        border-bottom: 1px solid @portal-tabs-border;
        overflow: hidden;
        .main-tabs-container {
            position: relative;
            height: @portal-tabs-height;
        }
        .main-tabs-control {
            height: @portal-tabs-height;
            overflow: hidden;
            background-color: @portal-tabs-bg;
            .control-item-link,
            a.control-item-link {
                position: relative;
                display: block;
                float: left;
                border-right: 1px solid @portal-tabs-border;
                height: 32px;
                line-height: 32px;
                padding: 0 12px;
                background-color: @portal-tabs-bg;
                color: @portal-tabs-color !important;
                font-size: @font-size-base;
                &:hover,
                &:active {
                    color: @portal-tabs-color;
                    background: @portal-tabs-hover-bg;
                }
            }
        }
        ul.ui-tabs-nav {
            margin-bottom: 0;
            padding: 0;
            border-bottom: 0;
            height: @portal-tabs-height;
            overflow: hidden;
            background-color: @portal-tabs-bg;
            border: none;
            &:extend(.clearfix all);
            li {
                position: relative;
                display: block;
                float: left;
                border-right: none;
                a {
                    &,
                    &:hover,
                    &:active {
                        &::before {
                            display: none;
                        }
                    }
                }
                &.ui-tabs-paging-prev {
                    margin-left: 0;
                    border-right: 1px solid @portal-tabs-border;
                }
                &.ui-tabs-paging-next {
                    border-left: 1px solid @portal-tabs-border;
                }
                &.ui-state-default {
                    border-right: 0;
                }
                a {
                    display: block;
                    position: relative;
                    height: @portal-tabs-height;
                    margin-right: 0;
                    margin-bottom: 0;
                    padding: 0px 12px;
                    line-height: @portal-tabs-height;
                    background-color: transparent;
                    color: @portal-tabs-color !important;
                    font-size: @font-size-base;
                    white-space: nowrap;
                    outline: none;
                    border: 0;
                    border-right: 1px solid @portal-tabs-border;
                    &:hover,
                    &:focus {
                        padding: 0px 12px;
                        text-decoration: none;
                        color: @portal-tabs-color;
                        border: 0;
                        background: @portal-tabs-hover-bg;
                        border-right: 1px solid @portal-tabs-border;
                    }
                }
                &.ui-tabs-active>a {
                    &,
                    &:hover,
                    &:focus {
                        padding: 0 12px;
                        color: @portal-tabs-color;
                        border: 0;
                        background: @portal-tabs-active-bg;
                        cursor: default;
                        border-right: 1px solid @portal-tabs-border;
                    }
                }
                &.ui-tabs-disabled>a {
                    color: @gray-brighter;
                    &:hover,
                    &:focus {
                        color: @gray-brighter;
                        text-decoration: none;
                        background-color: transparent;
                        cursor: not-allowed;
                        border-right: 1px solid @portal-tabs-border;
                    }
                }
                &.ui-state-disabled>a {
                    &,
                    &:hover,
                    &:focus {
                        padding: 0px 12px;
                        border: 0;
                        cursor: not-allowed;
                    }
                }
                &.ui-tabs-paging-next {
                    float: right;
                }
                &.ui-tabs-paging-prev>a,
                &.ui-tabs-paging-next>a {
                    width: @nav-tabs-paging-btn-width;
                    border: 0;
                    &:hover,
                    &:focus {
                        color: @nav-tabs-paging-btn-color;
                        background: transparent;
                    }
                }
                &.ui-tabs-paging-disabled {
                    &.ui-tabs-paging-prev>a,
                    &.ui-tabs-paging-next>a {
                        color: @gray-brighter;
                        cursor: not-allowed;
                    }
                }
            }
        }
    }
    .ui-tabs-hide {
        display: none !important;
    }
    .ui-tabs-panel-absolute {
        position: absolute;
        top: (@line-height-computed + (@portal-base-padding * 2));
        right: 0;
        bottom: 0;
        left: 0;
        overflow-y: auto;
    }
    .iconfont {
        font-size: @font-size-large;
    }
    .ui-tabs-main-close {
        float: none;
        text-shadow: none;
        width: 14px;
        height: 14px;
        text-align: center;
        line-height: 14px;
        border-radius: 50%;
        opacity: 1;
        color: @text-weak-color;
        .glyphicon {
            font-family: "iconfont" !important;
            font-size: @font-size-small !important;
            top: 0;
            &.glyphicon-remove-sign {
                &::before {
                    content: '\e77d';
                }
            }
        }
        .iconfont {
            font-size: @font-size-base !important;
        }
        &:hover,
        &:focus {
            background: @brand-danger;
            color: #fff;
            opacity: 1;
            .iconfont {
                color: #FF645A;
            }
        }
    }
}


/* common */

.header-bg {
    background: url("../img/portal_head_bg_l.png") repeat-y left top @portal-primary;
}

.header {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 200;
    min-width: @portal-min-width;
    height: @portal-header-height;
    border-bottom: none;
    .logo {
        float: left;
        height: @portal-header-height;
        text-align: center;
        a {
            padding: 0 @padding-base-horizontal;
            display: inline-block;
            cursor: default;
            &,
            img {
                height: @portal-header-height;
            }
            &:hover {
                background-color: @portal-header-height;
            }
        }
    }
    .all-menu {
        float: left;
        width: 120px;
        text-align: center;
        a {
            display: block;
            padding: 0 @portal-large-padding;
            height: @portal-header-height;
            line-height: @portal-header-height;
            color: #fff;
            font-size: @font-size-base;
            .iconfont {
                margin-right: @padding-base-vertical;
                position: relative;
                top: 1px;
                font-size: 16px;
            }
            &.active,
            &:hover {
                background-color: @portal-topbar-hover-left;
            }
            &:focus,
            &.active {
                color: #fff;
                background-color: @portal-topbar-active-left;
            }
        }
    }
}


/* 头部样式 */

.search-bar {
    position: relative;
    width: auto;
    height: @portal-header-height;
    padding: 0 @padding-base-horizontal;
    overflow: hidden;
    .search-bar-main {
        position: relative;
        width: 80%;
        height: 24px;
        max-width: 320px;
        border-radius: 12px;
        background: @block-content-bg;
        top: 10px;
        transition: .2s;
        &.activated {
            top: 0;
            height: @portal-header-height;
            width: 100%;
            max-width: none;
            border-radius: 0;
            input {
                height: @portal-header-height;
                line-height: @portal-header-height;
            }
        }
    }
    .iconfont {
        display: block;
        position: absolute;
        left: 8px;
        top: 50%;
        margin-top: -8px;
        color: @text-weak-color;
        font-size: 18px;
    }
    input {
        width: 100%;
        height: 24px;
        line-height: 24px;
        padding-left: 30px;
        color: @text-weak-color;
        font-size: @font-size-base;
        border: none;
        background: none;
    }
    &.active {
        padding-left: 20px;
        height: @portal-header-height;
        background-color: @portal-hover-primary;
        .icon-return {
            position: absolute;
            top: 0;
            right: 10px;
            width: 90px;
            height: @portal-header-height;
            text-align: right;
            background: url("../../../image/icon-return.png");
            background-repeat: no-repeat;
            background-position: right center;
        }
        .topbar-search-dropdown {
            display: block;
        }
        .icon {
            display: none;
        }
        .search-icon {
            display: none;
        }
    }
}

.topbar-search-dropdown {
    display: none;
    position: fixed;
    right: 0;
    left: 0;
    z-index: 100;
    border: 1px solid @portal-border-color;
    border-radius: 0 0 @border-radius-base @border-radius-base;
    background: @block-content-bg;
    box-shadow: 0 4px 4px #B1B6BF;
    .panel {
        padding: 0;
        border: none;
        border-bottom: 1px solid @divide-color;
        border-radius: 0;
        &:last-child {
            margin-bottom: 0;
            border-bottom: none;
        }
        &+.panel {
            margin-top: 0;
        }
    }
    .result-list {
        .item {
            margin-bottom: @padding-base-vertical;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

.search-bar-result {
    .panel-heading {
        .ico-switch {
            color: @gray;
            font-weight: normal;
            font-size: 12px;
        }
    }
    .panel-title {
        color: @gray;
    }
    .panel-body {
        padding: 0;
    }
    .result-list {
        .item {
            font-size: @gray-darker;
            padding: @portal-base-padding @portal-large-padding;
            &:hover {
                background-color: @gray-lighter;
            }
        }
        .item-keyword {
            color: @brand-primary;
        }
        .item-tips {
            color: @gray;
            font-weight: normal;
            font-size: @font-size-base;
        }
    }
    .panel-footer {
        padding: @portal-base-padding @portal-base-padding;
        .pagination {
            margin: 0;
        }
    }
}


/* 顶部搜索条 */


/* 用户区域开始 */

.topbar-info {
    margin: 0;
    background: url("../img/portal_head_bg_r.png") no-repeat left top @portal-topbar-right;
    padding-left: 120px;
    .topbar-notice {
        position: relative;
        .badge {
            position: absolute;
            top: 4px;
            right: 8px;
            font-weight: lighter;
            background-color: #FFBE2D;
        }
    }
    .topbar-info-item {
        a {
            display: inline-block;
            padding: @portal-small-padding @portal-large-padding;
            height: @portal-header-height;
            line-height: @portal-header-height;
            font-size: 18px;
            color: @portal-text-color;
            &:hover {
                background: @portal-topbar-hover-right;
            }
            &:focus,
            .active {
                background: @portal-topbar-active-right;
            }
            .iconfont {
                position: relative;
                top: -2px;
                font-size: 24px;
            }
        }
    }
    .topbar-info-user {
        position: relative;
        height: @portal-header-height;
        .topbar-info-user-a {
            position: relative;
            display: block;
            padding: @portal-small-padding @portal-large-padding 0 @portal-large-padding;
            height: @portal-header-height;
            &:hover {
                background: @portal-topbar-hover-right;
            }
            &:focus,
            &.active {
                background: @portal-topbar-active-right;
            }
            .user-img {
                position: absolute;
                left: @portal-large-padding;
                top: @portal-small-padding;
                z-index: 2;
                &,
                img {
                    width: 32px;
                    height: 32px;
                }
            }
            .user-cont {
                padding-left: 32 + @portal-base-padding;
                font-size: @font-size-base;
                text-align: left;
                line-height: @line-height-normal;
                .user-cont-job {
                    font-size: 14px !important;
                    color: #fff;
                    padding-bottom: @portal-small-padding;
                }
                .user-cont-name {
                    color: #92A1BB;
                }
            }
        }
        .topbar-align-right {
            top: @portal-header-height;
            right: 0;
            left: auto;
            z-index: 2000;
        }
    }
}

.topbar-info-dropdown-memu {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    top: @portal-header-height;
    width: auto;
    height: 270px;
    border: 1px solid @portal-border-color;
    background: @block-content-bg;
    box-shadow: @dropdown-shadow;
    .dropdown-memu-row {
        margin: 0;
        padding: 0;
        position: relative;
        height: 100%;
        .panel {
            background: none;
            border: none;
            margin-bottom: 0;
            .panel-heading {
                background: none;
                border: none;
            }
            .panel-footer {
                border: none;
                background: none;
            }
        }
        .dropdown-memu-col {
            padding: 0;
            position: relative;
            height: 100%;
            border-right: 1px solid @border-color;
            overflow-y: auto;
            overflow-x: hidden;
            &:last-child {
                border-right: none;
            }
        }
        .dropdown-group-main {
            position: absolute;
            z-index: 1;
            left: 0;
            right: 0;
            bottom: 48px;
            top: 0;
            overflow-x: hidden;
            overflow-y: auto;
        }
        .dropdown-group-control {
            border-top: 1px solid @divide-color;
            height: 48px;
            padding: @padding-small-vertical*3 @padding-large-horizontal @padding-small-vertical*3 @padding-base-horizontal;
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
            .group-control-tips {
                display: inline-block;
                height: @input-height-base;
                line-height: @input-height-base;
            }
        }
        .dropdown-memu-col.user-info-col {
            background: #e7ecf3;
            .user-info-dt {
                color: @text-color;
            }
            .user-info-dd {
                color: @text-weak-color;
            }
            .user-info-dl,
            .user-info-dt,
            .user-info-dd {
                margin: 0;
                padding: 0;
            }
            .user-info-dl {
                margin-bottom: @padding-small-horizontal;
            }
            .user-info-cont,
            .user-info-address {
                position: relative;
                padding-left: 40px;
                .user-info-icon {
                    position: absolute;
                    left: 0;
                    top: 0;
                    font-size: 16px;
                }
            }
            .user-info-cont {
                margin-bottom: @padding-large-horizontal;
                .user-info-dt,
                .user-info-dd {
                    display: inline;
                }
                .user-info-icon {
                    color: @brand-success;
                }
            }
            .user-info-address {
                .user-info-dt,
                .user-info-dd {
                    display: block;
                }
                .user-info-dt {
                    font-weight: normal;
                    color: @text-weak-color;
                }
                .user-info-dd {
                    font-weight: bold;
                    color: @text-color;
                }
                .user-info-icon {
                    color: @brand-primary;
                }
            }
        }
        .post-tabs-list {
            margin: -@padding-small-vertical -@padding-small-horizontal;
            overflow: hidden;
            .post-tabs-list {
                margin: @padding-small-vertical @padding-small-horizontal;
                overflow: hidden;
            }
            .post-label-link {
                position: relative;
                display: inline-block;
                padding: @padding-small-vertical @padding-base-horizontal;
                border-radius: @border-radius-base;
                color: @text-weak-color;
                overflow: hidden;
                &:hover {
                    color: @brand-primary;
                    background: @block-hover-bg;
                }
                &.active {
                    color: @text-color;
                    background: @block-active-bg;
                }
                &.activated {
                    color: @brand-primary;
                    background: @block-hover-bg;
                    &::before {
                        content: '';
                        position: absolute;
                        left: 0;
                        top: 0;
                        width: 0;
                        height: 0;
                        border-width: 0 0 8px 8px;
                        border-style: solid;
                        border-color: transparent @brand-danger;
                    }
                }
                &.disable {
                    background: none;
                    color: @text-disabled-color;
                    cursor: not-allowed;
                }
            }
        }
    }
    .topbar-user-entrance-list {
        margin: -@padding-small-vertical;
        overflow: hidden;
        >li {
            float: left;
            width: 33.333333%;
        }
        a {
            display: block;
            .entrance-tit {
                margin: @padding-small-vertical;
                text-align: center;
                padding: @padding-small-vertical @padding-base-horizontal;
                border-radius: @border-radius-base;
                background: #EBEEF5;
                color: @text-weak-color;
                display: block;
                text-align: center;
                color: @text-weak-color;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
            }
            &:hover,
            &.active {
                .entrance-tit {
                    color: @brand-primary;
                    background: @block-hover-bg;
                }
            }
        }
    }
    .toper-user-menu {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1;
        bottom: 48px;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .toper-user-sign-out {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 1;
        height: 48px;
        border-top: 1px solid @divide-color;
        .btn-sign-out {
            border-radius: 0;
            border: none;
            width: 100%;
            color: @brand-danger;
            display: block;
            height: 47px;
        }
    }
}


/* 用户区域结束 */


/*系统菜单展开开始*/

.topbar-dropdown-box {
    display: none;
    position: fixed;
    padding-bottom: 32px;
    font-size: @font-size-large;
    top: @portal-header-height;
    right: 0;
    left: 0;
    width: 100%;
    height: 80%;
    overflow: hidden;
    z-index: 200;
    .y-row {
        position: relative;
        width: 100%;
        height: 100%;
        border-bottom: 1px solid @border-color;
        background: #fff;
        box-shadow: @dropdown-shadow;
        .menu-close {
            position: absolute;
            bottom: -30px;
            left: 50%;
            margin-left: -30px;
            width: 60px;
            height: 30px;
            padding: 0 @portal-base-padding;
            text-align: center;
            border: 1px solid @border-color;
            border-radius: 0 0 @border-radius-base @border-radius-base;
            background: #f0f1f3;
            border-top: 0;
            z-index: 10;
            &:before {
                font-family: "iconfont" !important;
                content: '\e7f3';
                font-size: 20px;
                color: #88a0be;
            }
        }
        &::after {
            clear: both;
            content: " ";
            display: block;
            font-size: 0;
            height: 0;
            visibility: hidden;
        }
        .ltely-menu {
            float: left;
            position: relative;
            width: 180px;
            height: 100%;
            background: #fafafa;
            border-right: 1px solid @border-color;
            overflow-x: hidden;
            overflow-y: auto;
            .ltely-menu-title {
                margin: @portal-base-padding;
                padding: 6px 10px;
                color: #2D4678;
                font-size: @font-size-large;
            }
            .left-main {
                position: relative;
                min-height: 100%;
            }
            .ltely-list {
                padding: 0 8px 62px 8px;
                li {
                    padding-left: 25px;
                    height: 32px;
                    line-height: 32px;
                    &:hover {
                        background: #f0f0f0;
                        border-radius: @border-radius-base;
                    }
                    a {
                        display: block;
                        color: #2D4678;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        overflow: hidden;
                    }
                }
            }
            .clear-all-btn {
                position: absolute;
                left: 50%;
                bottom: 24px;
                margin-left: -44px;
                width: 86px;
                height: 22px;
                line-height: 22px;
                color: @portal-text-color;
                text-align: center;
                border-width: 1px solid #C3CEE4;
                border-radius: 12px;
                &:hover {
                    background: #c3cee4;
                    color: #586b8d;
                }
            }
        }
        .general-menu {
            float: left;
            width: 180px;
            height: 100%;
            background: #fff;
            overflow-x: hidden;
            overflow-y: auto;
            border-right: 1px solid @border-color;
            .general-menu-box {
                padding: @portal-base-padding;
            }
            .genral-search {
                height: 32px;
                border-width: 1px;
                border-style: solid;
                border-color: #C3CEE4;
                background: #fff;
                border-radius: @border-radius-base;
                input {
                    padding-left: 5px;
                    width: 135px;
                    height: 30px;
                    border: none;
                    background: none;
                    color: #8492AC;
                    vertical-align: middle;
                }
                .iconfont {
                    vertical-align: middle;
                    color: @portal-text-color;
                }
            }
            .general-list {
                margin-top: @portal-base-padding;
                li {
                    padding: @padding-base-vertical @padding-base-horizontal;
                    border-radius: @border-radius-base;
                    position: relative;
                    a {
                        display: block;
                        color: #2D4678;
                        cursor: pointer;
                    }
                    &:hover {
                        background: #f0f0f0;
                    }
                    &.active {
                        background: @brand-primary;
                        a {
                            color: #fff;
                            padding-right: 30px;
                        }
                    }
                    &.has-level {
                        &:hover {
                            background: #f0f0f0;
                            &:before {
                                font-family: "iconfont";
                                content: '\e7d1';
                                position: absolute;
                                font-size: 20px;
                                width: 20px;
                                height: 20px;
                                line-height: 20px;
                                overflow: hidden;
                                right: @portal-base-padding;
                                top: 50%;
                                margin-top: -10px;
                                color: #2D4678;
                            }
                        }
                        &.active {
                            background: @brand-primary;
                            &:before {
                                font-family: "iconfont";
                                content: '\e7d1';
                                position: absolute;
                                font-size: 20px;
                                width: 20px;
                                height: 20px;
                                line-height: 20px;
                                overflow: hidden;
                                right: @portal-base-padding;
                                top: 50%;
                                margin-top: -10px;
                                color: #fff;
                            }
                        }
                    }
                }
            }
        }
        .topbar-menu-content {
            position: relative;
            height: 100%;
            overflow: hidden;
            .topbar-menu-search {
                padding: @portal-large-padding;
                box-sizing: content-box;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                height: 30px;
                z-index: 20;
                background: #fff;
                &~.topbar-menu-category {
                    padding-top: 40 + @portal-large-padding;
                }
            }
            .topbar-menu-category {
                padding: 8px 20px;
                position: relative;
                z-index: 1;
                height: 100%;
                overflow-x: hidden;
                overflow-y: auto;
                .ng-title {
                    padding: 8px 0 6px;
                    padding-left: 8px;
                    font-size: 20px;
                    font-weight: normal;
                    color: @brand-primary;
                }
                ul {
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    li {
                        &.ng-item {
                            a {
                                display: block;
                                padding: @portal-small-padding @portal-base-padding;
                                color: #223761;
                                border-radius: @border-radius-base;
                                &:hover {
                                    color: #00A8EC;
                                    background-color: #E8EBEF;
                                }
                            }
                        }
                    }
                    &::after {
                        clear: both;
                        content: " ";
                        display: block;
                        font-size: 0;
                        height: 0;
                        visibility: hidden;
                    }
                }
                .topbar-product-all-item.search-info {
                    .ng-title {
                        font-size: @font-size-base;
                        color: @gray;
                    }
                    ul {
                        li {
                            &.ng-item {
                                a {
                                    color: @gray-darker;
                                    &:hover {
                                        color: @gray-darker;
                                        background-color: #E8EBEF;
                                    }
                                }
                                .search-kw-txt {
                                    color: @brand-primary;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}


/*系统菜单展开结束*/


/*左侧菜单开始*/

.copyright {
    padding: @portal-base-padding @portal-large-padding;
    height: @portal-copyright-height;
    color: #B9BED1;
    font-size: @font-size-base;
    text-align: center;
    background: #fff;
    position: absolute;
    bottom: 0;
    right: 0;
    left: @portal-sidebar-width;
    z-index: 1;
    &.view-sidebar-close {
        left: 56px;
    }
    &.view-sidebar-none {
        left: 0;
    }
}

.view-framework-body {
    .main-tabs-nav {
        position: fixed;
        top: @portal-header-height;
        right: 0;
        left: @portal-sidebar-width;
        z-index: 100;
    }
    .view-framework-sidebar {
        position: fixed;
        width: @portal-sidebar-width;
        top: @portal-header-height;
        bottom: 0;
        left: 0;
        z-index: 10;
        background-color: @portal-sidebar-bg;
        border-right: 1px solid @border-color;
        .sidebar-container {
            position: relative;
            height: 100%;
            overflow-x: hidden;
            overflow-y: auto;
        }
        .sidebar-nav-tabs {
            height: @portal-sidebar-item-height;
            border-bottom: 1px solid @divide-color;
            padding: 0 @padding-base-horizontal;
            li {
                width: 50%;
                float: left;
                a {
                    display: block;
                    position: relative;
                    font-size: @font-size-large;
                    padding: 0 @padding-base-horizontal;
                    text-align: center;
                    color: @text-weaker-color;
                    height: @portal-sidebar-item-height;
                    line-height: @portal-sidebar-item-height;
                    &:hover,
                    &.active {
                        color: @text-color;
                        &::before {
                            content: '';
                            position: absolute;
                            bottom: 0;
                            left: @padding-base-horizontal;
                            right: @padding-base-horizontal;
                            border-bottom: 2px solid @brand-primary;
                        }
                    }
                }
            }
        }
        .sidebar-fold {
            height: @portal-tabs-height;
            padding: @portal-base-padding;
            text-align: center;
            background-color: @portal-sidebar-bg;
            cursor: pointer;
            border-bottom: 1px solid @portal-border-color;
            .iconfont {
                font-size: 20px;
                color: @portal-text-color;
            }
        }
        .sidebar-nav {
            position: absolute;
            top: @portal-sidebar-item-height;
            left: 0;
            right: 0;
            bottom: 0;
            overflow-x: hidden;
            overflow-y: auto;
            ul {
                li {
                    margin: 0;
                    padding: 0;
                    border-bottom: 1px solid @divide-color;
                    a {
                        display: block;
                        padding-left: @padding-large-horizontal;
                        height: @portal-sidebar-item-height;
                        line-height: @portal-sidebar-item-height;
                        padding-right: 20px;
                        color: @text-weak-color;
                        position: relative;
                        font-size: @font-size-large;
                        .nav-title {
                            height: @portal-sidebar-item-height;
                            white-space: nowrap;
                            text-overflow: ellipsis;
                            overflow: hidden;
                            display: block;
                        }
                        &:hover {
                            background-color: @block-hover-bg;
                        }
                        &:focus,
                        &.active {
                            background-color: @block-active-bg;
                            &:before {
                                font-family: "iconfont";
                                content: '\e7d1';
                                position: absolute;
                                right: @padding-base-horizontal;
                                top: 0;
                                font-size: 20px;
                            }
                        }
                    }
                }
            }
        }
    }
    .view-framework-product {
        padding-top: @portal-header-height + @portal-tabs-height;
        padding-left: @portal-sidebar-width;
        .view-framework-product-tabs {
            padding: @portal-large-padding;
        }
    }
    &.view-sidebar-close {
        .view-framework-sidebar {
            display: none; // .sidebar-nav{
            // 	.sidebar-nav-title{
            // 		text-align: center;
            // 		padding: 0;
            // 	}
            // 	ul{
            // 		padding:0;
            // 		li{
            // 			a{
            // 				border-radius: 0;
            // 				.nav-icon{
            // 					width: 56px;
            // 				}
            // 				.nav-title{
            // 					display: none;
            // 				}
            // 				&:focus, &.active{
            // 					background-color: #0096E6;
            // 					color:#fff;
            // 					.iconfont, .el{
            // 						color:#fff;
            // 					}
            // 					&:before{
            // 						display: none;
            // 					}
            // 				}
            // 			}
            // 		}
            // 	}
            // }
        }
        .view-framework-product {
            padding-left: 0;
        }
        .main-tabs-nav {
            left: 0;
        }
    }
    &.view-sidebar-none {
        .view-framework-sidebar {
            display: none;
        }
        .view-framework-product {
            padding-left: 0;
        }
        .main-tabs-nav {
            left: 0;
        }
    }
}


/*左侧菜单结束*/